@import "variables";

/**/
img{
	border:0;
	max-width:100%;
	height: auto;
}
a{
	color: $color-1;
	text-decoration: none;
	transition: color 0.2s, background 0.2s, border 0.2s;
	&:hover{
		color: $color-3;
		text-decoration: underline;
	}
	&:focus{
	 	text-decoration: none;
	}
}

h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5{
	margin-top: 0px;
    color: $color-1;
}
h1, .h1{
	font-weight: 300;
	font-size: 56px;
	line-height: 1.3em;
	text-transform: capitalize;
	margin-bottom: 0.7em;
	strong{
		font-weight: 600;
	}
}
h2, .h2{
	font-weight: 300;
	font-size: 32px;
	line-height: 1.5em;
	margin-bottom: 0.7em;
}
h3, .h3{
	font-weight: 300;
	font-size: 28px;
	line-height: 1.5em;
	margin-bottom: 0.7em;
}
h4, .h4{
	font-weight: 600;
	font-size: 18px;
	margin-bottom: 0.7em;
}

strong, b{
	font-weight: 600;
}

iframe{
	display: block;
}

/* table */
table{
	border-collapse: collapse;
	width: 100%;
	margin: 0px 0 15px;
}
table th{
	padding: 5px 5px;
	text-align: center;
	text-transform: uppercase;
	font-size: 14px;
	line-height: 1.3em;
}
table td{
	padding: 0px 5px;
	font-size: 12px;
}

.text-wrap{
	line-height: 1.8em;
	@include clearfix;
	a:not(.btn){
		color: $color-3;
		&:hover{
			color: $color-1;
		}
	}
	strong, b{
		color: $color-1;
	}
	h1, .h1{
		margin-bottom: 0.8em;
	}
	h4, .h4{
		margin-bottom: 1.9em;
	}
	p, ul, ol{
		margin-bottom: 1.8em;
	}
	ul, ol{
		list-style-position: inside;
	}
	h2, .h2{
		margin-bottom: 0.8em;
	}
	p+h3, ul + h3, ol + h3, table + h3, p+h2, ul + h2, ol + h2, table + h2{
		margin-top: 1em;
	}
	img{
		margin-bottom: 1.8em;
	}
	hr{
		margin-bottom: 1.5em;
		border-color: $color-1;
		opacity: 0.12;
		border-top: none;
	}
}
.bg-dark{
	.text-wrap{
		hr{
			border-color: #fff;
			opacity: 0.2;
		}
	}
}

.page{
	h1{
		margin-bottom: 0.55em;
	}
}

@media (max-width: 1199px){
	h1, .h1{
		font-size: 50px;
	}
}
@media (max-width: 991px){
	#wrapper{
		padding-top: 0 !important;
	}
	h1, .h1{
		font-size: 44px;
	}
}
@media (max-width: 767px){
	h1, .h1{
		font-size: 40px;
	}
	.text-wrap{
		h1, .h1{
			line-height: 1.4em;
			margin-bottom: 1em;
		}
		h2{
			font-size: 28px;
		}
		h4, .h4{
			margin-bottom: 1.2em;
		}
	}
}
@media (max-width: 575px){
	.btn{
		min-width: 200px;
	}
}


/* wp-style */
@media (min-width: 1200px){
	.wp-block-column{
		padding-right: 30px;
	}
}
@media (min-width: 768px){
	.wp-block-column:not(:first-child) {
	    margin-left: 30px;
	}
}
@media (max-width: 767px){
	.wp-block-column {
	    flex-basis: 100%!important;
	    margin-left: 0 !important;
	}
}